.container {
    display: flex;
    flex-wrap: wrap;
    .items {
        width: 25%;
        height: 25vw;
        transition: all 0.3s ease-in-out;
        &:hover {
            transform: scale(1.03);
            z-index: 2;
            box-shadow: rgba(0, 0, 0, 0.328) 0 0 1000px;
            cursor: pointer;
        }
    }
}
